<template>
  <div class="detail">
    <div class="header">
      <div class="header-back" @click="back">
        <img src="/static/img/login-back.png" alt>
      </div>
      <div class="header-title">详细数据</div>
      <div class="header-more"></div>
    </div>
    <div class="detail-data-box">
      <div class="detail-data">
        <span class="detail-data-title">合约</span>
        <span class="detail-data-num white">美黄金</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">期数</span>
        <span class="detail-data-num white">{{clNum}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">最新价</span>
        <span class="detail-data-num red">{{quoteData[3]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">涨跌额</span>
        <span class="detail-data-num red">3.2</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">涨跌幅</span>
        <span class="detail-data-num red">0.26%</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">当前手数</span>
        <span class="detail-data-num white">10</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">买价</span>
        <span class="detail-data-num green">{{quoteData[13]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">卖价</span>
        <span class="detail-data-num green">{{quoteData[11]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">买量</span>
        <span class="detail-data-num white">{{quoteData[14]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">成交量</span>
        <span class="detail-data-num white">{{quoteData[6]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">持仓量</span>
        <span class="detail-data-num white">{{quoteData[7]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">卖量</span>
        <span class="detail-data-num white">{{quoteData[12]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">开盘价</span>
        <span class="detail-data-num red">{{quoteData[2]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">昨结价</span>
        <span class="detail-data-num white">{{quoteData[10]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">最高价</span>
        <span class="detail-data-num red">{{quoteData[4]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">最低价</span>
        <span class="detail-data-num green">{{quoteData[5]}}</span>
      </div>
      <div class="detail-data">
        <span class="detail-data-title">平均价</span>
        <span class="detail-data-num red">1233.2</span>
      </div>
      <div class="detail-data detail-data-content">
        <span>更多数据正在计算</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "detail",
  data() {
    return {
      websock: {},
      loginData: {},
      commodityData: {},
      quoteData: {},
      subscribeData: {}
    };
  },
  computed: {
    clNum() {
      if (this.quoteData[0]) {
        return this.quoteData[0].slice(3);
      }
    }
  },
  methods: {
    back() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    scrollTop() {
      window.scrollTo(0, 0);
    },
    initWebSocket(url) {
      //初始化websocket
      const wsuri = url;
      this.websock = new WebSocket(wsuri); //这里面的this都指向vue
      this.websock.onopen = this.websocketopen;
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onclose = this.websocketclose;
      this.websock.onerror = this.websocketerror;
    },
    websocketopen() {
      //打开
      console.log("WebSocket连接成功");
      let loginData = this.login();
      this.websocketsend(loginData);
      let commodityData = this.commodity();
      this.websocketsend(commodityData);
      let subscribeData = this.subscribe();
      this.websocketsend(subscribeData);
    },
    websocketsend(data) {
      //数据发送
      this.websock.send(data);
    },
    websocketonmessage(e) {
      //数据接收
      let response = JSON.parse(e.data);
      if (response.method == "on_rtn_quote") {
        console.log(response);
      }
      switch (response.method) {
        case "on_rsp_login":
          this.loginData = response.data;
          break;
        case "on_rsp_commodity_list":
          this.commodityData = response.data;
          break;
        case "on_rtn_quote":
          this.quoteData = response.data;
          break;
        case "on_rsp_subscribe":
          this.subscribeData = response.data;
          break;

        default:
          break;
      }
    },
    websocketclose(e) {
      //关闭
      console.log("WebSocket关闭");
      console.log(e);
    },
    websocketerror(e) {
      //失败
      console.log("WebSocket连接失败");
    },

    // 登录函数
    login() {
      // 登录所传数据
      let loginSendData = {
        method: "req_login",
        req_id: "1231",
        data: {
          user_name: "chenlin",
          password: "a123456",
          protoc_version: "2.0"
        }
      };

      return JSON.stringify(loginSendData);
    },

    // 获取商品信息
    commodity() {
      // 获取商品信息
      let commodityData = {
        method: "req_commodity_list",
        req_id: "1231",
        data: {}
      };

      return JSON.stringify(commodityData);
    },

    // 订阅函数
    subscribe() {
      // 订阅信息
      let subscribeData = {
        method: "req_subscribe",
        req_id: "1231",
        data: {
          mode: "MODE_LEVEL1",
          contract_list: ["FO_GC"]
        }
      };

      return JSON.stringify(subscribeData);
    },

    // 获取行情推送
    quote() {
      // 获取商品信息
      let commodityData = {
        method: "on_rtn_quote",
        req_id: "1231",
        data: {}
      };

      return JSON.stringify(commodityData);
    }
  },

  created() {
    //页面刚进入时开启长连接
    this.initWebSocket("ws://quote.vs.com:8888");
  },
  mounted() {
    this.scrollTop();
  },
  destroyed: function() {
    //页面销毁时关闭长连接
    this.websocketclose();
  }
};
</script>
<style scoped>
@import url("../../../../static/css/detail.css");
</style>



