import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login/login.vue'
import detail from '@/components/main/detail/detail.vue'
import register from '@/components/register/register.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
      path: '/',
      name: 'login',
      component: login
    }, {
      path: '/detail',
      name: 'detail',
      component: detail,
    },
    {
      path: '/register',
      name: 'register',
      component: register,
    },
  ]
})



