<template>
  <div class="login">
    <div class="header">
      <div class="header-back" @click="back">
        <img src="/static/img/login-back.png" alt>
      </div>
      <div class="header-title">登录</div>
      <div class="header-more"></div>
    </div>

    <div class="login-form">
      <van-field
        v-model="loginForm.phonenum"
        class="input-item"
        clearable
        label="手机号:"
        maxlength="11"
      />

      <van-field
        v-model="loginForm.password"
        type="password"
        class="input-item"
        label="密码:"
        clearable
      />
    </div>
    <div class="login-btn">
      <button class="loginBtn" @click="submit">登录</button>
    </div>
    <div class="register">
      <router-link to="/register">注册账号</router-link>
    </div>

    <div class="dialog-box" v-if="dialogViewAble">
      <div class="dialog">
        <div class="dialog-header">
          <div class="dialog-header-title">
            <span>请输入图形验证码</span>
          </div>
          <div class="dialog-header-close" @click="closeDialog">
            <img id="imgCodeEl" src="/static/img/close.png" alt>
          </div>
        </div>
        <div class="dialog-body">
          <div class="imgcode-box">
            <van-field
              v-model="imgcode"
              class="input-item imgcode-input"
              label="验证码:"
              clearable
              maxlength="20"
            />
            <span class="imgcode">
              <img :src="imgSrc" alt>
            </span>
          </div>
        </div>
        <div class="dialog-footer">
          <span class="dialog-footer-cancle">取消</span>
          <span class="dialog-footer-confirm" @click="submitWithDialog">确定</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import req from "@/lib/req";
export default {
  name: "login",
  data() {
    return {
      loginStatus: false,
      dialogViewAble: false,
      // 表单数据源
      loginForm: {
        // 手机号
        phonenum: "",
        // 密码
        password: ""
      },
      imgcode: "",
      imgSrc:
        "http://platform-trade.dktai.cn/loginAndRegister/getImgCode.html?mobile=",
      failNum: "0"
    };
  },
  computed: {
    phoneForImgSrc() {
      return this.loginForm.phonenum;
    }
  },
  watch: {
    phoneForImgSrc(newValue, oldValue) {
      this.imgSrc += newValue;
    }
  },
  methods: {
    back() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    scrollTop() {
      window.scrollTo(0, 0);
    },
    closeDialog() {
      this.dialogViewAble = false;
    },
    async submit() {
      let vm = this;
      // 失败三次要获取图形验证码
      if (vm.failNum == "3") {
        vm.dialogViewAble = true;
      } else {
        vm.dialogViewAble = false;
        let res = await req.mobileLoginWithoutImg(
          vm.loginForm.phonenum,
          vm.loginForm.password,
          "0123456789012345678901234567890123456789012345678901234567890123456789012345678901234567890123456789",
          vm
        );
        if (res.data.code == "1" && res.data.success) {
          vm.$message({
            type: "success",
            content: res.data.message
          });
        } else {
          vm.$message({
            type: "error",
            content: res.data.message
          });
          if (res.data.data.failNum) {
            vm.failNum = res.data.data.failNum;
          }
        }
      }
    },
    async submitWithDialog() {
      let res = await req.mobileLogin(
        vm.loginForm.phonenum,
        vm.loginForm.password,
        "0123456789012345678901234567890123456789012345678901234567890123456789012345678901234567890123456789",
        vm.imgcode,
        vm
      );
      if (res.data.code == "1" && res.data.success) {
        vm.$message({
          type: "success",
          content: res.data.message
        });
      } else {
        vm.$message({
          type: "error",
          content: res.data.message
        });
        if (res.data.data.failNum) {
          vm.failNum = res.data.data.failNum;
        }
      }
    }
  },
  created() {},
  mounted() {}
};
</script>

<style scoped>
@import url("../../../static/css/login.css");
</style>



