import axios from 'axios'
import qs from 'qs'

const req = {};
req.loginRegisterUrl = '/api';
req.dataUrl = '/data';

req.axios = axios;


async function doReq(method, vm, url, data) {
  let res;
  try {
    await axios[method](url, data, {
      timeout: 1000 * 60
    }).then(function (resp) {
      return res = resp;
    }).catch((e) => {
      vm.$message({
        type: 'error',
        content: '网络错误,请检查网络连接',
        duration: 1500,
      });
    })
  } catch (e) {
    vm.$message({
      type: 'error',
      content: '网络连接失败，请刷新',
      duration: 1500,
    });
    throw 'error';
  }
  return res;
}

// 获取图形验证码
req.getImgCode = async function (vm) {
  return await doReq('get', vm, req.loginRegisterUrl + '/loginAndRegister/getImgCode.html', {
    params: {
      mobile: '13132077790',
    }
  });
}

// 获取手机验证码
req.getSmsCode = async function (mobile, type, imageCode, vm) {
  return await doReq('post', vm, req.loginRegisterUrl + '/loginAndRegister/getSmsCode', qs.stringify({
    mobile: mobile,
    type: type,
    imageCode: imageCode
  }));
}

// 获取手机验证码
req.register = async function (mobile, password, resource, code, vm) {
  return await doReq('post', vm, req.loginRegisterUrl + '/loginAndRegister/register', qs.stringify({
    mobile: mobile,
    password: password,
    resource: resource,
    code: code
  }));
}

// 登录无图形
req.mobileLoginWithoutImg = async function (mobile, password, clientId, vm) {
  return await doReq('post', vm, req.loginRegisterUrl + '/loginAndRegister/mobileLogin', qs.stringify({
    mobile: mobile,
    password: password,
    clientId: clientId,
  }));
}

// 登录
req.mobileLogin = async function (mobile, password, clientId, imgCode, vm) {
  return await doReq('post', vm, req.loginRegisterUrl + '/loginAndRegister/mobileLogin', qs.stringify({
    mobile: mobile,
    password: password,
    clientId: clientId,
    imgCode: imgCode
  }));
}

export default req;



