import Vue from 'vue';
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loginStatus: false,
  },
  loginInfo: {

  },
  mutations: {
    updateLoginStatus(state, payload) {
      state.loginStatus = payload;
    },
    updateLoginInfo(state, payload) {
      state.loginInfo = payload;
    },
  }
})



