<template>
  <transition name="fade">
    <div :class="[`message-${type}`, 'center']" v-if="visible" :style="positionStyle">
      <v-icon :name="icon" class="icon" size="16"/>
      <p class="message-content" v-html="content"></p>
      <span v-if="showClose" @click.stop="messageClose">
        <v-icon name="times" class="close-icon" size="16"></v-icon>
      </span>
    </div>
  </transition>
</template>

<script>
import popupbox from "../mixins/popupbox";
import iconMap from "../mixins/iconMap";
export default {
  mixins: [popupbox],
  props: {
    type: { type: String, default: "info" },
    content: { default: String, required: true },
    duration: { type: Number, default: 2000 },
    verticalOffset: { type: Number, default: 32 },
    showClose: { type: Boolean, default: false }
  },
  data() {
    return {
      timer: null
    };
  },
  computed: {
    positionStyle() {
      return {
        ["top"]: `${this.verticalOffset}px`
      };
    },
    icon() {
      return iconMap[this.type];
    }
  },
  mounted() {
    this.startTimer();
  },
  beforeDestroy() {
    this.stopTimer();
  },
  methods: {
    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          this.close();
          this.$emit("messageClose");
        }, this.duration);
      }
    },
    stopTimer() {
      if (this.timer) clearTimeout(this.timer);
    },
    messageClose() {
      this.close();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/styles/message.scss";
</style>


