<template>
  <div class="register">
    <div class="header">
      <div class="header-back" @click="back">
        <img src="/static/img/login-back.png" alt>
      </div>
      <div class="header-title">注册</div>
      <div class="header-more"></div>
    </div>

    <div class="register-form">
      <div>
        <van-field
          v-model="registerForm.phonenum"
          class="input-item"
          clearable
          label="手机号:"
          maxlength="20"
        />
      </div>
      <div class="vercode-box">
        <van-field
          v-model="registerForm.code"
          class="input-item code-input"
          label="验证码:"
          clearable
          maxlength="20"
        />
        <button class="get-code" @click="showDialog">获取验证码</button>
      </div>
      <div>
        <van-field
          v-model="registerForm.password"
          type="password"
          class="input-item"
          label="设置密码:"
          clearable
        />
      </div>
      <div class="register-btn">
        <button class="registerBtn" @click="register">注册</button>
      </div>
      <div class="login">
        <router-link to="/">登录账号</router-link>
      </div>
    </div>

    <div class="dialog-box" v-if="dialogViewAble">
      <div class="dialog">
        <div class="dialog-header">
          <div class="dialog-header-title">
            <span>请输入图形验证码</span>
          </div>
          <div class="dialog-header-close" @click="closeDialog">
            <img id="imgCodeEl" src="/static/img/close.png" alt>
          </div>
        </div>
        <div class="dialog-body">
          <div class="imgcode-box">
            <van-field
              v-model="imgcode"
              class="input-item imgcode-input"
              label="验证码:"
              clearable
              maxlength="20"
            />
            <span class="imgcode">
              <img :src="imgSrc" alt>
            </span>
          </div>
        </div>
        <div class="dialog-footer">
          <span class="dialog-footer-cancle">取消</span>
          <span class="dialog-footer-confirm" @click="getSmsCode">确定</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import req from "@/lib/req";

export default {
  name: "register",
  data() {
    return {
      loginStatus: false,
      dialogViewAble: false,
      // 表单数据源
      registerForm: {
        // 账户名
        phonenum: "",
        // 手机验证码
        code: "",
        // 密码
        password: ""
      },
      imgcode: "",
      imgSrc:
        "http://platform-trade.dktai.cn/loginAndRegister/getImgCode.html?mobile="
    };
  },
  computed: {
    phonenumForImgSrc() {
      return this.registerForm.phonenum;
    }
  },
  watch: {
    phonenumForImgSrc(newValue, oldValue) {
      this.imgSrc += newValue;
    }
  },
  methods: {
    back() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    },
    scrollTop() {
      window.scrollTo(0, 0);
    },
    showDialog() {
      this.dialogViewAble = true;
    },
    closeDialog() {
      this.dialogViewAble = false;
    },
    async getSmsCode() {
      let vm = this;
      let res = await req.getSmsCode(
        vm.registerForm.phonenum,
        "1",
        vm.imgcode,
        vm
      );
      if (res.data.code == "1" && res.data.success) {
        vm.dialogViewAble = false;
        vm.$message({
          type: "success",
          content: "发送成功"
        });
      } else {
        vm.$message({
          type: "error",
          content: res.data.message
        });
      }
    },
    async register() {
      let vm = this;
      console.log("fick");
      let res = await req.register(
        vm.registerForm.phonenum,
        vm.registerForm.password,
        "12345678876543211234567887654321",
        vm.registerForm.code,
        vm
      );
      if (res.data.code == "1" && res.data.success) {
        vm.$message({
          type: "success",
          content: "注册成功"
        });
      } else {
        vm.$message({
          type: "error",
          content: res.data.message
        });
      }
    }
  },
  async mounted() {
    this.scrollTop();
  }
};
</script>
<style scoped>
@import url("../../../static/css/register.css");
</style>


